@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css");
 
@media only screen and (max-width: 400px) {

  .circular-icon {
    border-radius: 50%;
    padding: 10px; /* Adjust this value as needed for the desired size */
    transition: background-color 0.3s ease;
  }
 
  .chat-icon{
    position: fixed;
    bottom:32vh;
    right: 2vw;
    width: 29vw;
    height: 4%;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 3vw;
    padding:1vw;
    z-index: 999;
 
  }
 
   
  .down-icon{
    position: fixed;
    height: 4vh;
    width: 8vw;
    background-color: #e80c31;
    border-radius: 30px;
    bottom: 31.5vh;
    color:white;
    font-size:2vh;
    right: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 999;
  }
 
 
  .inner-box {
    display: flex;
    flex-direction: column;
    width: 94vw;
    /* height:600px; */
    border: 1px solid #ccc;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  
    /* margin: 20px auto;  */
  }




  
 
  .chatbot-header{

    height:3.5vh;
 
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.4vh;
    font-weight: bold;
  }
 
  .header-content{
    display: flex;
    align-items: center;
  }
 
  .bot-logo{
    height: 40px;
     width: 40px;
    margin-right: 10px;
    border-radius: 50% ;
  }
 
  .bot-name{
    margin-left: 60px;
    text-align: center;
    font-family: sans-serif;
    font-size: 16px;
  }
  .close-icon{
    cursor: pointer;
    font-size: 24px;
    margin-right: 10px;
    display: flex;
  }
 
  .App {
    display: flex;
    flex-direction: column;
    height: 42vh;
    padding: 1vw;
  }
 
  .chat-container {
    flex-grow: 1;
    overflow: auto; /* Enable automatic scrolling */
    min-height: 0;
    max-height: calc(100vh - 140px); /* Adjust this value as needed */
     /* Align messages to the top */
  }

  
 /* Hide the scrollbar track */
 .chat-container::-webkit-scrollbar-track {
  background-color: transparent;
}
/* Define scrollbar width and color */
.chat-container::-webkit-scrollbar {
  width: 8px; /* Adjust as needed */
  background-color: transparent;
}
/* Define scrollbar thumb appearance */
.chat-container::-webkit-scrollbar-thumb {
  background-color: transparent;
  border-radius: 4px; /* Rounded corners */
}
/* Define scrollbar thumb on hover appearance */
.chat-container::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.3); /* Adjust opacity as needed */
}
 
  .message {
    display: flex;
    flex-direction: column;
    margin: 5px;
    align-items: baseline;
 
  }
 
  .bot-icon{
    height: 25px;
    width: 25px;
    border-radius:50%;
    object-fit:cover;
    /* border-radius: 50% ; */
  }
 
  .user-message {
   
    align-self: flex-end;
    padding: 8px;
    border-radius: 10px;
    margin-bottom: 5px;
    font-family: sans-serif;
    font-size: 14px;
  }
 
  .bot-message {
   
    
    padding: 8px;
    border-radius: 10px;
    margin-bottom: 5px;
    font-family: sans-serif;
    font-size: 14px;
    word-wrap: break-word;
    max-width: 360px;
    white-space: pre-line;
  }
 
  .feedback-icon{
    display: flex;
    gap: 15px;
  }
  /* icon.css */
  .thumbs-up-icon ,.thumbs-down-icon{
    
    transform: scale(1.1);
    transition: transform 0.3s;
  }
 
  .default-icon {
  
    transform: scale(1);
  }
 
  .refined-query {
    font-style: italic;
    font-size: 12px;
    color: #888;
    align-self: flex-end;
  }
 
  .input-container {
    display: flex;
    align-items: center;
    padding: 1.5vh;
    height:3vh;
    gap:5px;
    border-top: 2px solid #ccc;
  }
 
  input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 20px;
    font-family: sans-serif;
    font-size: 14px;
  }
 
  .user-icon {
    margin: 2px 6px;
    align-self: flex-end;
    
  }
  .user-bot-icon{
    height: 25px;
    width: 25px;
    border-radius:50%;
    object-fit:cover;
  }
  .empty-input {
    border: 1px solid #e80c31;
    color: #e80c31;
  }
 
  /* Additional style for the "Enter Valid Query" placeholder */
  .empty-input::placeholder {
    color: #e80c31;
  }
 
  /* Add this CSS in your stylesheets */
  .chatbot-overlay {
    position: fixed;
    right: 3%;
    bottom:  36%;
    background-color: white;
    z-index: 999;
   
  }
 
  .send-icon{
  font-size: 20px;
  cursor: pointer;
 
  }
}
 
